.main__title {
  display: inline-block;
  line-height: 64px;
  color: rgba(0, 0, 0, .65);
  font-size: 20px;
  font-weight: 600;
  text-align: center;
}

.main__dropdown {
  float: right;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.main__dropdown-title {
  min-width: 100px;
  font-size: 15px;
  text-align: center;
}

.main__dropdown-title-text {
  color: #1890ff;
  margin-right: 5px;
}

#screen-loading {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background-color: #fff;
  z-index: 10000;
}

.screen-loading__viewbox {
  width: 20vw;
  height: 20vh;
  position: absolute;
  top: calc(40% - 10vh);
  left: calc(50% - 10vw);
}

.screen-loading__text {
  color: #454545;
  font-family: 'Microsoft YaHei UI','Microsoft YaHei',DengXian,SimSun,'Segoe UI',Tahoma,Helvetica,sans-serif;
  font-size: 18px;
  font-weight: 200;
  text-align: center;
}
/* PC端 */
.layout {
	height: 100%;
  background-color: #f0f2f5;
  overflow: hidden;
}

.layout-header {
  height: 64px;
  background-color: #fff;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, .2);
  padding: 0 8px;
}

.layout-sider {
  float: left;
  width: 200px;
  height: 100%;
  background-color: #001529;
}

.layout-sider__top {
  height: 64px;
}

.layout-sider__logo {
  width: 50px;
  height: 50px;
  position: relative;
  top: 50%;
  vertical-align: top;
  -webkit-transform: translateY(-50%) ;
          transform: translateY(-50%) ;
  -webkit-animation: layout-logo-spin infinite 20s linear;
          animation: layout-logo-spin infinite 20s linear;
}

.layout-sider__name {
  display: inline-block;
  height: 32px;
  padding: 0 10px;
  background-color: #334454;
  color: rgba(255, 255, 255, .8);
  font-size: 16px;
  line-height: 32px;
  position: relative;
  top: 50%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}

.layout-content {
  height: calc(100% - 80px);
  background-color: #fff;
  box-shadow: 0 1px 6px rgba(0, 0, 0, .2);
  box-sizing: border-box;
  margin: 8px;
  overflow-x: hidden;
  overflow-y: auto;
}

@-webkit-keyframes layout-logo-spin {
  from {
    -webkit-transform: translateY(-50%) rotate(0deg);
            transform: translateY(-50%) rotate(0deg);
  }
  
  to {
    -webkit-transform: translateY(-50%) rotate(360deg);
            transform: translateY(-50%) rotate(360deg);
  }
}

@keyframes layout-logo-spin {
  from {
    -webkit-transform: translateY(-50%) rotate(0deg);
            transform: translateY(-50%) rotate(0deg);
  }
  
  to {
    -webkit-transform: translateY(-50%) rotate(360deg);
            transform: translateY(-50%) rotate(360deg);
  }
}
